import React from "react";
import { Link } from "react-router-dom";
import { DateTime, Interval } from "luxon";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Button from "@material-ui/core/Button";

export default function Log(props) {
  const { deleteCurrentTask, saveCurrentTask, tasksList, classes } = props;
  const { tableRow, noTasks, tasksButton, hideCellIfSmallScreen, centerText } = classes;

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell className={hideCellIfSmallScreen}>â„–</TableCell>
          <TableCell className={centerText}>Task</TableCell>
          <TableCell className={hideCellIfSmallScreen}>Time start</TableCell>
          <TableCell className={hideCellIfSmallScreen}>Time end</TableCell>
          <TableCell className={hideCellIfSmallScreen}>Time spend</TableCell>
          <TableCell className={centerText}>Info</TableCell>
          <TableCell className={centerText}>Delete</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {tasksList.length > 0 ? (
          tasksList.map(task => (
            <TableRow key={task.id} className={tableRow}>
              <TableCell className={hideCellIfSmallScreen} component="th" scope="row">
                {task.id}
              </TableCell>
              <TableCell className={centerText}>{task.taskName}</TableCell>
              <TableCell className={hideCellIfSmallScreen}>
                {DateTime.fromISO(task.timerStartTime).toFormat("HH:mm:ss")}
              </TableCell>
              <TableCell className={hideCellIfSmallScreen}>
                {DateTime.fromISO(task.timerStopTime).toFormat("HH:mm:ss")}
              </TableCell>
              <TableCell className={hideCellIfSmallScreen}>
                {Interval.fromDateTimes(
                  DateTime.fromISO(task.timerStartTime),
                  DateTime.fromISO(task.timerStopTime)
                )
                  .toDuration()
                  .toFormat("hh:mm:ss")}
              </TableCell>
              <TableCell className={centerText}>
                <Button
                  className={tasksButton}
                  variant="contained"
                  size="small"
                  onClick={() => saveCurrentTask(task)}
                  component={Link}
                  to={`/tasks/${task.id}`}
                >
                  Info
                </Button>
              </TableCell>
              <TableCell className={centerText}>
                <Button
                  className={tasksButton}
                  variant="contained"
                  size="small"
                  onClick={() => deleteCurrentTask(task.id)}
                >
                  Delete
                </Button>
              </TableCell>
            </TableRow>
          ))
        ) : (
          <TableRow className={tableRow}>
            <TableCell className={noTasks} colSpan={7}>
              No tasks to show
            </TableCell>
          </TableRow>
        )}
      </TableBody>
    </Table>
  );
}
